
function Navbar(){
    return(
        <nav class="navbar navbar-expand-lg navbar-light bg-light mr-10">
            <div class="container-fluid">
                <a class="navbar-brand" href="#">Links</a>
                <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
                </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav me-auto mb-2 mb-lg-0">
                    <li class="nav-item">
                        <a class="nav-link active" aria-current="page" href="#">Design</a>
                    </li>
                </ul>
                <form class="d-flex">
                    <input disabled class="form-control me-2" type="search" placeholder="https://link.snipfeed.co/anshuman" aria-label="Search"/>
                    <button class="btn btn-outline-success" type="submit">Copy</button>
                </form>
                </div>
            </div>
        </nav>
    )
}

export default Navbar;